@media (prefers-reduced-motion: no-preference) {
  .img {
    animation: App-logo-spin infinite 10s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.cal_body{
  padding: 25px;
  box-shadow: 0 0 30px rgba(12, 12, 12, 0.226);
  border-radius: 25px;
  
}
.cal_body span{
  font-size: 12px;
  text-align: left;
  padding:  23px 1px 15px;
}
.cal_body .display_area input[type="text"]{
  height: 80px;
  padding: 50px 7px 20px 10px;
  display: inline-block;
  border-radius: 15px;
  text-align: right;
  border: none;
  width: 100%;
  font-size: 40px;
  box-shadow: 1px 1px 12px -7px rgba(0, 0, 0, 0.838) inset;
}
input:focus{
  outline: none;
}
.cal_body .button_area{
  text-align: center;
  padding: 10px;
}

.cal_body .button_area .button{
  text-align: center;
  border: 2px solid rgba(239, 239, 239, 0.144);

  box-shadow: 0 0 10px rgba(132, 132, 132, 0.272);
  height: 70px;
  width: 70px;
  line-height: 3;
  border-radius: 20px;
  
 
}
.cal_body .button_area .button{
  font-size: 24px;
}
.cal_body .button_area .button button{
  background: none;
  border: none;
  line-height: 3;

}

.cal_body .button_area .button button:hover{
  background-color: rgba(177, 177, 177, 0.1);
  color: black;
  width: 70px;
  height: 70px;
  border-radius: 20px;
}
.bg_blackk{
  background-color: rgb(8, 7, 7);
  color: #fff;
}

